'use client';
import React, { useState } from 'react';
import NewsCard from './NewsCard';

interface NewsItem {
  id: string;
  title: string;
  description: string;
  imageUrl?: string;
  date: string;
  category: string;
}

interface NewsListProps {
  news: NewsItem[];
}

const NewsList: React.FC<NewsListProps> = ({ news }) => {
  const [categoryFilter, setCategoryFilter] = useState<string>('All');
  const [sortBy, setSortBy] = useState<string>('Date');

  const categories = ['All', ...new Set(news.map(item => item.category))];

  const filteredNews = categoryFilter === 'All' ? news : news.filter(item => item.category === categoryFilter);

  const sortedNews = [...filteredNews].sort((a, b) => {
    if (sortBy === 'Date') {
      return new Date(b.date).getTime() - new Date(a.date).getTime();
    }
    return 0;
  });

  return (
    <div className="container mx-auto py-8">
      <div className="flex justify-between items-center mb-4">
        <div className="flex gap-2">
          <select
            className="px-4 py-2 rounded-md bg-white border border-gray-300 focus:outline-none focus:ring-2 focus:ring-accent"
            value={categoryFilter}
            onChange={(e) => setCategoryFilter(e.target.value)}
          >
            {categories.map(category => (
              <option key={category} value={category}>{category}</option>
            ))}
          </select>
          <select
            className="px-4 py-2 rounded-md bg-white border border-gray-300 focus:outline-none focus:ring-2 focus:ring-accent"
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
          >
            <option value="Date">Sort by Date</option>
          </select>
        </div>
        <input
          type="search"
          placeholder="Search articles"
          className="px-4 py-2 rounded-md bg-white border border-gray-300 focus:outline-none focus:ring-2 focus:ring-accent"
        />
      </div>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {sortedNews.map(item => (
          <NewsCard
            key={item.id}
            title={item.title}
            description={item.description}
            imageUrl={item.imageUrl}
            date={item.date}
            category={item.category}
          />
        ))}
      </div>
    </div>
  );
};

export default NewsList;
