import React from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";

interface NewsCardProps {
  title: string;
  description: string;
  imageUrl?: string;
  date: string;
  category: string;
}

const NewsCard: React.FC<NewsCardProps> = ({ title, description, imageUrl, date, category }) => {
  return (
    <Card className="bg-secondary shadow-md rounded-lg overflow-hidden">
      <CardHeader>
        <CardTitle>{title}</CardTitle>
        <CardDescription>{category} - {date}</CardDescription>
      </CardHeader>
      <CardContent>
        {imageUrl && <img src={imageUrl} alt={title} className="w-full h-48 object-cover mb-4 rounded-md" />}
        <p>{description}</p>
      </CardContent>
    </Card>
  );
};

export default NewsCard;
